import React, { useState } from "react";
import * as Yup from "yup";
import { makeStyles } from "@material-ui/core/styles";
import {
  Box,
  Card,
  FormControl,
  FormControlLabel,
  FormLabel,
  InputLabel,
  MenuItem,
  Radio,
  RadioGroup,
  Select,
  TextField,
  Typography,
} from "@material-ui/core";
import toast from "react-hot-toast";
import axios from "axios";
import { Formik } from "formik";
import Save from "../Buttons/Save";
import ResetButton from "../Buttons/ResetButton";
import Design from "../Design";
import { Navigate, useLocation, useNavigate } from "react-router";
import userService from "../apis/userService";
import {customAxios} from "../apis/Axios";
const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      margin: theme.spacing(1),
      width: "25ch",
      margin: "17px",
    },
  },
  typography: {
    padding: "17px",
  },
  head: {
    height: "200vh",
    paddingLeft: 30,
    paddingRight: 30,
    paddingTop: 10,
    background: "none",
  },
}));

export default function AddClass() {
  const classes = useStyles();
  const { state } = useLocation();
  const Navigate = useNavigate();
  // console.log(",,,", state.state);
  if (state == null) {
    var invoice = { subjectName: " ", subjectType: " ", subjectCode: " " };
  } else {
    state.invoice;
    var { invoice } = state;
  }
  // var {row}=state;
  return (
    <>
      <Card className={classes.head}>
        <Formik
          // enableReinitialize
          initialValues={{
            subjectName: invoice.subjectName || "",
            subjectType: invoice.subjectType || "",
            subjectCode: invoice.subjectCode || "",
          }}
          validationSchema={Yup.object().shape({
            subjectName: Yup.string().max(255).required("Rollno is required"),
          
          })}
          onSubmit={async (values) => {
            await new Promise((r) => setTimeout(r, 500));
            const data = {
              ...values,
              id: invoice.id ? invoice.id : "",
            };
            try {

              customAxios.postCall("books/api/subjects",{...data}).then((res) => {
                if(res.status===400){
                      toast.success("Subject Already Exist");

                  }else{

                    res.json;

                      toast.success(invoice.id ? " Update Data " : "Add SuccessFully");
                      Navigate("../subject_list");
                  }
              });
            } catch (err) {
              console.log(err);
            }
          }}
        >
          {({
            errors,
            handleBlur,
            handleChange,
            handleSubmit,
            isSubmitting,
            dirty,
            touched,
            values,
            handleReset,
          }) => (
            <div>
              <Design text="Add Subject" dashText="Add Subject" />
              <Box style={{ paddingLeft: 20 }} boxShadow={20} color="black">
                <div className={classes.typography}>
                  <Typography variant="h5">
                    <strong>Add Subject</strong>
                  </Typography>
                </div>
                <form noValidate onSubmit={handleSubmit}>
                  <div>
                    <TextField
                      autoComplete="off"
                      id="subjectName"
                      label="Subject Name"
                      style={{ margin: 8, width: "95%" }}
                      variant="outlined"
                      type="text"
                      error={Boolean(touched.subjectName && errors.subjectName)}
                      helperText={touched.subjectName && errors.subjectName}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      value={values.subjectName}
                    />
                    <FormControl
                      style={{ paddingLeft: 10 }}
                      component="fieldset"
                    >
                      {/* <FormLabel component="legend">Payment Duration</FormLabel> */}
                      <RadioGroup
                        row
                        aria-label="subjectType"
                        name="subjectType"
                        id="subjectType"
                        value={values.subjectType}
                        onChange={handleChange}
                      >
                        <FormControlLabel
                          value="theory"
                          control={<Radio />}
                          label="theory"
                        />
                        <FormControlLabel
                          value="practicle"
                          control={<Radio />}
                          label="practicle"
                        />
                      </RadioGroup>
                    </FormControl>
                    <TextField
                      id="subjectCode"
                      label="Subject Code"
                      style={{ margin: 8, width: "95%" }}
                      fullWidth
                      error={Boolean(touched.subjectCode && errors.subjectCode)}
                      helperText={touched.subjectCode && errors.subjectCode}
                      onBlur={handleBlur}
                      onChange={handleChange}
                      margin="normal"
                      value={values.subjectCode}
                      variant="outlined"
                    />
                  </div>
                  <Save
                    text={invoice.id ? " Update Class " : "Save "}
                    variant="contained"
                    size="medium"
                    color="success"
                    type="submit"
                    disabled={isSubmitting}
                  />

                  <ResetButton
                    text="Reset"
                    variant="contained"
                    size="medium"
                    color="primary"
                    type="button"
                    onClick={handleReset}
                    disabled={!dirty || isSubmitting}
                  />
                </form>
              </Box>
            </div>
          )}
        </Formik>
        <Typography style={{ marginTop: 20 }}>
          Â© Copyrights <b>akkhor</b> 2019. All rights reserved. Designed by
          PsdBosS
        </Typography>
      </Card>
    </>
  );
}
